.stars, .twinkling, .clouds {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: block; }

.stars {
  background: #000 url(http://www.script-tutorials.com/demos/360/images/stars.png) repeat top center;
  background-size: cover;
  z-index: 0; }

.twinkling {
  background: transparent url(http://www.script-tutorials.com/demos/360/images/twinkling.png) repeat top center;
  background-size: cover;
  z-index: 1;
  -webkit-animation: move-twink-back 200s linear infinite;
  animation: move-twink-back 200s linear infinite; }

.clouds {
  background: transparent url(http://www.script-tutorials.com/demos/360/images/clouds3.png) repeat top center;
  background-size: cover;
  z-index: 3;
  -webkit-animation: move-clouds-back 200s linear infinite;
  animation: move-clouds-back 200s linear infinite; }

@keyframes move-twink-back {
  from {
    background-position: 0 0; }
  to {
    background-position: -10000px 5000px; } }

@-webkit-keyframes move-twink-back {
  from {
    background-position: 0 0; }
  to {
    background-position: -10000px 5000px; } }

@keyframes move-clouds-back {
  from {
    background-position: 0 0; }
  to {
    background-position: 10000px 0; } }

@-webkit-keyframes move-clouds-back {
  from {
    background-position: 0 0; }
  to {
    background-position: 10000px 0; } }

.contentWeb {
  position: absolute;
  z-index: 10;
  width: 100%; }

.CharacterList {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 0px; }

.LinkCard {
  text-decoration: none;
  margin: 5%;
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 0 3px #fff, 0 0 0 4px #fff, 3px -3px 30px #eacb1b,  -3px 3px 30px #ffb310;
          box-shadow: inset 0 0 0 3px #fff, 0 0 0 4px #fff, 3px -3px 30px #eacb1b,  -3px 3px 30px #ffb310; }

.WebTitle {
  text-align: center;
  font-family: 'Charmonman', cursive;
  color: white;
  font-size: 35px;
  text-shadow: 1px -1px 0 #f3e08b,  -1px 2px 1px #f7dd4d,  -2px 4px 1px #806a09; }
  @media all and (min-width: 768px) {
    .WebTitle {
      font-size: 80px; } }



